/// <reference path="../../main.scss" />

%body {
    font-size: 100%;
    font-family: $body-font;
    color: $primary-dark;
    @include font-size($reg-font-sizes);

    ::selection {
        background: $primary-medium;
        color: $white;
    }

    ::-moz-selection {
        background: $primary-medium;
        color: $white;
    }
}

body {
    @extend %body;
}

a {
    @include link-helper();
}

b, strong {
    font-weight: $font-weight-bold;
}

em, i {
    font-style: italic;
}

h1, h2, h3, h4, h5 {
    font-family: $heading-font;
    font-weight: $font-weight-normal;
}

h1 {
    @include font-size($x-large-font-sizes);
}

h2 {
    @include font-size($large-font-sizes);
}
 
h3 {
    @include font-size($medium-large-font-sizes);
}

h4 {
    @include font-size($medium-font-sizes);
}

h5 {
    @include font-size($reg-font-sizes);
}

h6 {
    font-family: $accent-font;
    @include font-size($small-font-sizes);
}

%styled-list {
    list-style-type: none;
    padding-left: 0;

    li {
        position: relative;
        padding-left: 1.325rem;

        &:before {
            content: '\002022';
            color: $primary-medium;
            margin-right: 1rem;
            position: absolute;
            left: 0;
            top: -1px;
        }

        & + li {
            margin-top: 1.5rem;
        }
    }

    @media print {
        list-style-type: disc;
        padding-left: 1.5rem;

        li {
            margin: 0;
            padding-left: unset;

            &:before {
                content: none;
                display: none;
            }

            & + li {
                margin: 0;
            }
        }
    }
}

blockquote {

}

.svg-icon {
    display: inline-block;
    font-size: 1em;
    width: 1em;
    height: 1em;
    line-height: 1;
    stroke-width: 0;
    stroke: currentColor;
    fill: currentColor;
    pointer-events: fill;
    vertical-align: text-top;
}


