/// <reference path="../../main.scss" />

%btn {
    display: inline-block;
    padding: $space $gap-sm;
    text-decoration: none;
    border: 1px solid transparent;
    font-family: $body-font, serif;
    cursor: pointer;
    @include font-size($small-font-sizes);

    &[disabled] {
        cursor: not-allowed;
    }
}

.btn, %btn-primary {
    @extend %btn;
    background-color: $blue;
    border-color: $blue !important;
    @include link-helper($white, $white);

    &:hover, &:active, &:focus {
        background-color: #388cd7;
    }
}

.btn-secondary, %btn-secondary {
    @extend %btn;
    background-color: $white;
    @include link-helper($blue, $white);
    border-color: $blue;

    &:hover, &:active, &:focus {
        background-color: $blue;
        border-color: $blue;
        color: $white;
    }
}

%un-button {
    background: transparent;
    border: 0;
    padding: 0;
    margin: 0;
    cursor: pointer;
    display: inline;
    color: $primary-dark;
    @include font-size($reg-font-sizes);
    text-align: left;
    @extend %body;

    &:hover, &:focus {
        background: transparent;
        color: $primary-dark;
    }
}



