﻿/// <reference path="../../main">

@mixin link-helper($color: $primary-dark, $contrast: $secondary-medium) {
    transition: $transition;
    color: $color;
    text-decoration: none;
    border-bottom: 1px solid $color;

    &:hover,
    &:active,
    &:focus {
        border-color: transparent;
    }
}


