﻿/// <reference path="../../main">

$elements-to-space: (
        'h3', 'h4', 'h5', 'h6', 'p', 'ul', 'ol'
    );

%responsive-top-margin {
    @include responsive-property('margin-top', $text-spacing-map);
}

.rich-text {
    max-width: $max-line-length;

    @each $element1 in $elements-to-space {
        @each $element2 in $elements-to-space {
            > #{$element1} + #{$element2} {
                @extend %responsive-top-margin;
            }
        }
    }

    ul {
        @extend %styled-list;
    }
}


