﻿/// <reference path="../../main">

body {
    display: flex;
    flex-direction: column;
}

header {
    flex: 0 0 auto;
}

footer {
    flex: 0 0 auto;
}

main {
    flex: 1 0 auto;
    background: $primary-light;
    margin-top: 3.75rem;

    @include media-min(medium) {
        margin-top: 8.4375rem;
    }
}

.skip-link {
    position: absolute;
    background: $primary-light;
    padding: $space;
    z-index: 1000;
    top: 0;
    left: 0;
    transform: translateY(-100%);
    &:focus {
        transform: none;
    }
}


